@keyframes right_a {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0%);
        opacity: 1;
    }
}

.show_right {
    animation: right_a .8s both;
}

@keyframes right {
    from {
        transform: translateX(0%);
        opacity: 1;
    }
    to {
        transform: translateX(-100%);
        opacity: 0;
    }
}

.hide_right {
    animation: right .8s both;
}

@keyframes left {
    from {
        transform: translateX(100%);
        display: none;
        opacity: 0;
    }
    to {
        transform: translateX(0%);
        display: block;
        opacity: 1;
    }
}

.show_left {
    animation: left .8s both;
}

@keyframes left_a {
    from {
        transform: translateX(0%);
        display: block;
        opacity: 1;
    }
    to {
        transform: translateX(100%);
        display: none;
        opacity: 0;
    }
}

.hide_left {
    animation: left_a .8s both;
}

